import React from 'react';
import { Field, reduxForm } from "redux-form";
import { ItemsAPI } from '../../api/api';
import c from './AddPlace.module.css'
import {requiredInput} from '../../validation/validation'

const AddForm = (props) => {
    return <div>
        <form className={c.fields} onSubmit={props.handleSubmit}>
            <div className={c.field_text}>
                <Field type="text" placeholder="ссылка на фото (не обязательно)" name="img" component={"input"} />
            </div>
            <div className={c.field_text}>
                <Field validate={requiredInput} type="text" placeholder="Название" name="name" component={"input"} />
            </div>
            <div className={c.field_text}>
                <Field validate={requiredInput} type="text" placeholder="Информация" name="text" component={"textarea"} />
            </div>
            <div className={c.field_text}>
                <Field validate={requiredInput} type="text" placeholder="Адрес" name="adress" component={"input"} />
            </div>
            <div className={c.field_radio}>
                <label><Field validate={requiredInput} name="placeType" component={'input'} type="radio" value="park" /> Парк</label>
                <label><Field validate={requiredInput} name="placeType" component={'input'} type="radio" value="nature" /> Природа</label>
                <label><Field validate={requiredInput} name="placeType" component={'input'} type="radio" value="arcitecture" /> Архитектура</label>
                <label><Field validate={requiredInput} name="placeType" component={'input'} type="radio" value="art" /> Исскуство</label>
            </div>
            <div className={c.field_button}>
                <button>Добавить</button>
            </div>
        </form>
    </div>
}

const AddFormReduxForm = reduxForm({ form: 'login' })(AddForm)

class AddPlace extends React.Component {

    onSubmit = (formData, dispatch, props) => {
        let type = formData.placeType;
        let img = formData.img ? formData.img : false;
        let dataToSend = {
            'img': img,
            'name': formData.name,
            'text': formData.text,
            'adress': formData.adress,
        }

        dispatch(props.reset())

        ItemsAPI.addItems(type, dataToSend)
            .then(function (response) {
                console.log(response);
            })
            .catch(function (error) {
                console.log(error);
            });
    }

    render() {
        return (
            <div>
                <AddFormReduxForm onSubmit={this.onSubmit} />
            </div>
        )
    }
}

export default AddPlace